package fmj.gamemenu

import fmj.Global
import fmj.characters.Player
import fmj.goods.BaseGoods
import fmj.graphics.TextRender
import fmj.graphics.Util
import fmj.views.BaseScreen
import fmj.views.GameNode

import graphics.Bitmap
import graphics.Canvas
import graphics.Rect

import java.Stack
import java.gbkBytes

class ScreenGoodsList(
        override val parent: GameNode,
        private val goodsList: List<BaseGoods>,
        private val itemSelectedListener: OnItemSelectedListener,
        private val mode: Mode): BaseScreen {

    private var description = "".gbkBytes()

    private var toDraw = 0 // 
    private var nextToDraw = 0 // 
    private val stackLastToDraw = Stack.create<Int>() // 

    private var firstDisplayItemIndex = 0 // 

    private var curItemIndex = 0 // 
    private var lastDownKey = -1

    enum class Mode {
        Sale,
        Buy,
        Use
    }

    interface OnItemSelectedListener {
        fun onItemSelected(goods: BaseGoods)
    }

    private fun resetDescription() {
        description = if (goodsList.isNotEmpty()) {
            goodsList[curItemIndex].description.gbkBytes()
        } else {
            "".gbkBytes()
        }
        nextToDraw = 0
        toDraw = nextToDraw
        stackLastToDraw.clear()
    }

    override fun willAppear() {
        if (goodsList.isEmpty()) {
            curItemIndex = 0
        } else if (curItemIndex >= goodsList.size) {
            curItemIndex = goodsList.size - 1
        }
        resetDescription()
    }

    override fun update(delta: Long) {
        if (goodsList.isEmpty()) {
            popScreen()
        }
    }

    override fun draw(canvas: Canvas) {
        // 160x96320x192
        val centerX = (Global.SCREEN_WIDTH - 160) / 2
        val centerY = (Global.SCREEN_HEIGHT - 96) / 2
        canvas.drawBitmap(bgImage, centerX, centerY)
        if (goodsList.isEmpty()) return

        while (curItemIndex >= goodsList.size) showPreItem()

        val g = goodsList[curItemIndex]

        TextRender.drawText(canvas, if (mode == Mode.Buy) ":" + Player.sMoney else ":" + g.goodsNum, centerX + 60, centerY + 2)
        TextRender.drawText(canvas, g.name, centerX + 69, centerY + 23)
        TextRender.drawText(canvas, "" + if (mode == Mode.Buy) g.buyPrice else g.sellPrice, centerX + 69, centerY + 40)
        Util.drawTriangleCursor(canvas, centerX + 4, centerY + 8 + 23 * (curItemIndex - firstDisplayItemIndex))

        var i = firstDisplayItemIndex
        while (i < firstDisplayItemIndex + itemNumberPerPage && i < goodsList.size) {
            goodsList[i].draw(canvas, centerX + 14, centerY + 2 + 23 * (i - firstDisplayItemIndex))
            i++
        }

        // 
        val adjustedDisplayRect = Rect(
            displayRect.left + centerX, 
            displayRect.top + centerY,
            displayRect.right + centerX, 
            displayRect.bottom + centerY
        )
        nextToDraw = TextRender.drawText(canvas, description, toDraw, adjustedDisplayRect)
    }

    private fun showNextItem() {
        ++curItemIndex
        if (curItemIndex >= firstDisplayItemIndex + itemNumberPerPage) {
            ++firstDisplayItemIndex
        }
        resetDescription()
    }

    private fun showPreItem() {
        --curItemIndex
        if (curItemIndex < firstDisplayItemIndex) {
            --firstDisplayItemIndex
        }
        resetDescription()
    }

    override fun onKeyDown(key: Int) {
        if (key == Global.KEY_UP && curItemIndex > 0) {
            showPreItem()
        } else if (key == Global.KEY_DOWN && curItemIndex + 1 < goodsList.size) {
            showNextItem()
        } else if (key == Global.KEY_PAGEDOWN) {
            val len = description.size
            if (nextToDraw < len) {
                stackLastToDraw.push(toDraw)
                toDraw = nextToDraw
            }
        } else if (key == Global.KEY_PAGEUP && toDraw != 0) {
            stackLastToDraw.pop()?.let {
                toDraw = it
            }
        }
        lastDownKey = key
    }

    override fun onKeyUp(key: Int) {
        if (key == Global.KEY_ENTER && lastDownKey == Global.KEY_ENTER) {
            itemSelectedListener.onItemSelected(goodsList[curItemIndex])
        } else if (key == Global.KEY_CANCEL) {
            popScreen()
        }
    }

    companion object {

        private val bgImage by lazy {
            val bmp = Bitmap.createBitmap(160, 96)
            val pts = floatArrayOf(40f, 21f, 40f, 95f, 40f, 95f, 0f, 95f, 0f, 95f, 0f, 5f, 0f, 5f, 5f, 0f, 5f, 0f, 39f, 0f, 39f, 0f, 58f, 19f, 38f, 0f, 57f, 19f, 57f, 19f, 140f, 19f, 41f, 20f, 140f, 20f, 41f, 21f, 159f, 21f, 54f, 0f, 140f, 0f, 40f, 95f, 159f, 95f, 40f, 57f, 160f, 57f, 40f, 58f, 140f, 58f, 40f, 59f, 159f, 59f, 41f, 20f, 41f, 95f, 42f, 20f, 42f, 95f, 159f, 21f, 159f, 57f, 159f, 59f, 159f, 96f)
            val c = Canvas(bmp)
            c.drawColor(Global.COLOR_WHITE)
            c.drawLines(pts, Util.sBlackPaint)
            TextRender.drawText(c, ":", 45, 23)
            TextRender.drawText(c, ":", 45, 40)
            bmp
        }

        private val displayRect = Rect(44, 61, 156, 94)

        private val itemNumberPerPage = 4 // 
    }
}
